#navbar {
    opacity: .5;
    padding: 0;
    border-bottom: 1px solid #5F9FF3;
    background-color: #F6F4F5;
    width: 100%;
}

#navbar button {
    border-left: 1px solid #5F9FF3;
    border-right: 1px solid #5F9FF3;
    width: 144%;
}

#navbar #time {
    font-size: 1.5em;
}

#Navbuttons {
    margin-right: -2.3em;
}

#Navbuttons button:hover {
    background-color: blue;
}

#brand {
    border-right: 1px solid #5F9FF3;
    padding-right: 2em;
}

.isActive {
    position: relative;
    z-index: 100;
    background-color: red;
}

.top {
    -webkit-animation-name: top;
            animation-name: top;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes top {
    0% {opacity: 0}
    100% {opacity: 50%}
}

@keyframes top {
    0% {opacity: 0}
    100% {opacity: 50%}
}

@media only screen and (max-width: 800px) {
    #navbar {
        position: fixed;
    }

    ul#Navbuttons {
        margin: 0;
    }

    #navbar button {
        border-left: 1px solid #5F9FF3;
        border-right: 1px solid #5F9FF3;
        width: 100%;
    }

    a#brand {
        padding-right: 32px;
        margin-left: 32px;
    }
}



.time {
    margin-top: 3rem;
}

#displyClock{
    margin: 0;
    padding: 0;
    max-width: none;
    height: 47rem;
}

#displyClock .container {
    overflow: hidden;
}

#displyClock span {
    font-size: 6em;
}

#timeClock {
    margin: 0;
    padding: 0;
    max-width: none;
    height: 22rem;
    background: url(/ReactProject/static/media/53096861-stock-vector-vector-modern-seamless-sacred-geometry-pattern-trippy-black-and-white-abstract-geometric-background-.ac647e94.jpg) repeat-x;
    width: 5076px; /* The image width times 3 */
    -webkit-animation: slide 10s linear infinite;
            animation: slide 10s linear infinite;
    position: relative;
    z-index: 100;
}

@-webkit-keyframes slide {
    0%{
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-1692px, 0, 0);
                transform: translate3d(-1692px, 0, 0); /* The image width */
    }
}

@keyframes slide {
    0%{
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-1692px, 0, 0);
                transform: translate3d(-1692px, 0, 0); /* The image width */
    }
}

