    height: 46em;
}

#form label{
    margin-left: 3.3em;
}

#form input {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    color: black;
    background-color: transparent;
    border-bottom: 4px black solid;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    padding: 0;
    outline: none;
}

#form #inputs {
    padding-top: 12em;
}

#form #input {
    margin-top: 11em;
    margin-left: 12em;
}

#form textarea {
    width: 88%;
}

.content {
    height: 0;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: grow;
            animation-name: grow;
}

.hidden {
    visibility: hidden;
}

@-webkit-keyframes grow {
    0% {height: 0}
    100% {height: 3rem}
}

@keyframes grow {
    0% {height: 0}
    100% {height: 3rem}
}

.show {
    -webkit-animation-name: show;
            animation-name: show;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
}

@-webkit-keyframes show {
    0% {opacity: 0}
    100% {opacity: 1}
}

@keyframes show {
    0% {opacity: 0}
    100% {opacity: 1}
}




